<template>
    <header class="header">
        <div class="intro">

            <h1 class="logo">
                <router-link to="/">George Shinkarev</router-link>
            </h1>

            <navigation v-if="$root.device.desktop"></navigation>

            <div class="space" v-if="$root.device.desktop"></div>

            <mobile-menu v-if="$root.device.mobile"></mobile-menu>

        </div>
    </header>
</template>

<script>
    import Navigation from '@/components/Navigation'
    import MobileMenu from '@/components/MobileMenu'

    export default {
        name: 'HeaderMain',

        components: {
            Navigation,
            MobileMenu
        }
    }
</script>


