<template>
    <div class="mobile-menu">

        <a href="#" class="mobile-menu__button" @click.prevent="openMenu()"></a>

        <nav class="mobile-menu__list" v-if="opened" @click="closeMenu()">
            <a href="#" class="mobile-menu__close" @click.prevent="closeMenu()"></a>
            <navigation></navigation>
        </nav>

    </div>
</template>

<script>
    import Navigation from '@/components/Navigation'

    export default {

        name: "MobileMenu",

        data: function () {
            return {
                opened: false
            }
        },

        components: {
            Navigation
        },

        //watch: {
        //    '$route' () {
        //        this.closeMenu();
        //    }
        //},

        methods: {

            openMenu: function() {
                this.opened = true;
                document.getElementsByTagName('body')[0].style = 'overflow-y: hidden;';
            },

            closeMenu: function() {
                this.opened = false;
                document.getElementsByTagName('body')[0].style = '';
            },

        }

    }
</script>

<style scoped>

</style>


