<template>
    <div>
        <share-buttons class="not-mobile" orientation="vertical"></share-buttons>

        <header-main></header-main>

        <main>

            <div class="intro">
                <slider v-if="$root.device.desktop"></slider>

                <div class="section-title article-heading">
                    <span v-if="$root.device.desktop">Последние посты</span>
                    <span v-else>Все последние посты</span>
                </div>
            </div>

            <div class="light-gray">
                <div class="intro">
                    <posts></posts>
                </div>
            </div>

        </main>

        <base-footer></base-footer>
    </div>
</template>

<script>
    import Slider from '@/components/Slider'
    import HeaderMain from '@/components/HeaderMain'
    import Posts from '@/components/Posts'
    import BaseFooter from '@/components/BaseFooter'
    import ShareButtons from '@/components/ShareButtons'

    export default {
        name: 'MainPage',

        components: {
            Slider,
            HeaderMain,
            Posts,
            BaseFooter,
            ShareButtons
        },

        mounted: function() {
            //console.log(this.$parent.tempkey);
        }
    }
</script>


