<template>
    <div>
        <share-buttons class="not-mobile" orientation="vertical"></share-buttons>

        <header-main></header-main>

        <main>

            <div class="intro" v-if="$root.device.mobile">
                <div class="section-title article-heading">
                    {{ $root.$children[0].getCategoryName($route.params.category) }}
                </div>
            </div>

            <div class="light-gray">
                <div class="intro">
                    <posts :category="$route.params.category"></posts>
                </div>
            </div>

        </main>

        <base-footer></base-footer>
    </div>
</template>

<script>
    import HeaderMain from '@/components/HeaderMain'
    import Posts from '@/components/Posts'
    import BaseFooter from '@/components/BaseFooter'
    import ShareButtons from '@/components/ShareButtons'

    export default {
        name: 'Category',

        components: {
            HeaderMain,
            Posts,
            BaseFooter,
            ShareButtons
        }

    }
</script>


